import React, { Component } from 'react';

let inlineStyle = {
  'background-image': 'radial-gradient(rgba(0, 0, 0, 0.25) 25%, rgba(0, 0, 0, 0) 55%)',
  'text-align': 'center'
}


class NotFoundPage extends Component {
  componentDidMount() {
    this.props.onOpenArticle('pageNotFound', false);
  }
  
  render() {
    return (
      <div className={`active ${this.props.articleTimeout ? 'timeout' : ''}`} style={inlineStyle}>
        <h1>NOT FOUND</h1>
        <p>
          You just hit a route that doesn&#39;t exist... the sadness.<br />
          <a href='/'>Let's go back home, shall we?</a>
        </p>
      </div>
    );
  }
}

export default NotFoundPage;



